import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Slider from '@material-ui/core/Slider';
import Grid from '@material-ui/core/Grid';

import { instance } from './Config';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import MapCell from './muiltiple/MapCell';
import { useStyles } from '../styles/Styles';

const JSONtable = {
    0: {
        0: {
            id: 5,
            color: "#666699",
        },
        1: {
            id: 3,
            color: "#cc9900",
        },
        2: {
            id: 4,
            color: "#669900",
        }
    },
    1: {
        0: {
            id: 5,
            color: "#666699",
        },
        1: {
            id: 6,
            color: "#3399ff",
        },
        2: {
            id: 5,
            color: "#666699",
        }
    },
    2: {
        0: {
            id: 5,
            color: "#666699",
        },
        1: {
            id: 5,
            color: "#666699",
        },
        2: {
            id: 5,
            color: "#666699",
        }
    },
    3: {
        0: {
            id: 5,
            color: "#666699",
        },
        1: {
            id: 5,
            color: "#666699",
        },
        2: {
            id: 5,
            color: "#666699",
        }
    },
};

function DreamMap(props) {
    const { lang, themeMode, history } = props;
    const classes = useStyles();
    const muiTheme = createMuiTheme(themeMode);
    const [cellSize, setCellSize] = React.useState(50);
    const [locations, setLocations] = React.useState({});
    
    const changeCellSize = (event, newValue) => {
        setCellSize(newValue);
    };

    const createTable = () => {
        let table = [];
        let i = 0;
        let j = 0;

        Object.keys(JSONtable).forEach(row => {
            let rows = [];
            Object.keys(JSONtable[row]).forEach(col => {
                rows.push(
                    <MapCell key={'cell' + i + j}
                        i={i}
                        j={j}
                        cellHeight={cellSize * 0.6}
                        cellWidth={cellSize * 0.6}
                        id={JSONtable[row][col].id}
                        color={JSONtable[row][col].color}
                        locations={locations}
                    />
                );
                i++;
            })
            table.push(
                <tr key={'row' + j} >
                    {rows}
                </tr>
            );
            j++;
            i = 0;
        });

        return table;
    }

    const loadTags = () => {
        instance.get("/gettags")
            .then(res => {
                setLocations(res.data);
            })
            .catch(err => {
                console.log(err)
            });
    };

    React.useEffect(() => {
        loadTags();
    }, []);

    return (
        <MuiThemeProvider theme={muiTheme}>
            <CssBaseline />
            <div className={classes.root}>
                <Grid className={classes.mainGridContainer}
                    container
                    direction="column"
                    justify="center"
                    alignItems="stretch" >
                    <Grid item xs={10} zeroMinWidth className={classes.mainGridDreamsBodyItem}>
                        <Container
                            style={{
                                paddingTop: '16px',
                                height: '83%',
                                width: '100%',
                                position: 'absolute',
                            }}
                        >
                            <div
                                style={{
                                    position: 'relative',
                                    height: '100%',
                                    width: '100%',
                                    overflow: 'auto',
                                    borderRadius: '4px',
                                }}
                            >
                                <table
                                    style={{
                                        margin: '0 auto',
                                        backgroundColor: 'rgb(196, 188, 78, 0.6)',
                                        borderRadius: '4px',
                                    }}
                                >
                                    <tbody>
                                        {createTable()}
                                    </tbody>
                                </table>
                            </div>
                        </Container>
                    </Grid>
                    <Grid item xs={1} zeroMinWidth className={classes.mainGridBodyItem}>
                        <div
                            style={{
                                position: 'absolute',
                                left: '50%',
                                top: '50%',
                                transform: 'translate(-50%, -50%)',
                                width: '60%',
                            }}
                        >
                            <Slider
                                min={6}
                                value={cellSize}
                                onChange={changeCellSize}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={1} zeroMinWidth className={classes.mainGridBodyItem}>
                        <Grid
                            container
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                        >
                            <Grid item xs={6} align="center">
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    className={classes.actionButton}
                                    onClick={() => { history.push("/luciddreams") }}
                                >
                                    {lang.currLang.buttons.close}
                                </Button>
                            </Grid>
                            <Grid item xs={6} />
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </MuiThemeProvider >
    )
}

DreamMap.propTypes = {
    themeMode: PropTypes.object.isRequired,
    lang: PropTypes.object.isRequired,
};

const mapStateToProps = store => {
    return {
        themeMode: store.themeMode,
        lang: store.lang,
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DreamMap);